import {NgModule} from '@angular/core';
import {CalculatorComponent}   from './calculator.component';
import {LoggedInService} from '../../shared/services/logged-in.service';
import {CalculatorService} from './services/calculator.service';
import {SharedModule} from '../../shared/modules/shared.module';
import {TemplateModule} from './templateAll/template.module';
import {BuilderService} from '../+builder/services/builder.service';
import {Routes, RouterModule} from "@angular/router";
import {SeoComponent} from './seo.component';

const CALCULATOR_ROUTES: Routes = [
  {
    path: '',
    children: [
      {
        path: '',
        component: CalculatorComponent
      },
      {
        path: ':name',
        component: SeoComponent
      }
    ]
  }
];

@NgModule({
    imports: [RouterModule.forChild(CALCULATOR_ROUTES) , SharedModule, TemplateModule],
    exports: [],
    declarations: [CalculatorComponent, SeoComponent],
    providers: [ CalculatorService, LoggedInService, BuilderService]
})
export class CalculatorModule { }



