import { Injectable } from '@angular/core';
import {App} from '../../+builder/models/model';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { BaseService } from '../../../shared/services/base.service';

@Injectable()
export class CalculatorService extends BaseService {

  constructor(private _http: Http) {
    super();
  }

  getCompanyApp(url: String, company: string): Observable<App> {
    let URL = this._url + '/builder/get_company_calculator/' + url + '/' + company;
    return this._http.get(URL, this.options)
      .map(this.extractData);
  }

  getApp(url: String): Observable<App> {
    let URL = this._url + '/builder/get_calculator/' + url;
    return this._http.get(URL, this.options)
      .map(this.extractData)
      .catch(this.handleError);
  }

  getAppResult(url: String): Observable<App> {
    let URL = this._url + '/builder/get_calculator_result/' + url;
    return this._http.get(URL, this.options)
      .map(this.extractData)
      .catch(this.handleError);
  }
}



