import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CalculatorService } from './services/calculator.service';

declare var ga: Function;
declare var jQuery: any;

@Component({
  selector: 'og-seo-calc',
  template: `<Temp *ngIf="tempName" [JSON_Template]="JSON_Template"></Temp>`,
  styles: [require('./calculator.component.css')],
})

export class SeoComponent implements OnInit {

  tempName: String;
  pageStatus: String;
  JSON_Template: any;
  private sub: any;

  constructor(private _calService: CalculatorService, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      let name = params['name'];
      if (name)
        this.initPage(name);
    });
  }

  initPage(name: string) {
    let url = window.location.href;
    let urlParts = url.split('/');

    this._calService.getCompanyApp(name, urlParts[2].split('.')[0])
      .subscribe(
        (response: any) =>{
            if (response.mode != 'PRIVATE') {
              this.JSON_Template = response;
              this.tempName = response.template;
            } else
              this.pageStatus = 'Private';
        },
        (error: any) => {
          console.log(error);
        }
      );
  }
}



